@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";
@import url('https://fonts.googleapis.com/css?family=Montserrat:400,700,800,900&display=swap');
@import url('https://fonts.googleapis.com/css?family=IBM+Plex+Sans:400,700,800,900&display=swap');
@font-face {
  font-family: 'Codec';
  font-style: normal;
  font-weight: 400;
  src: local('Codec'), local('Codec-Regular'), url(../fonts/codec/Codec-Cold-Regular-trial.ttf) format('truetype');
}
@font-face {
  font-family: 'Aller';
  font-style: normal;
  font-weight: 400;
  src: local('Aller'), local('Aller-Regular'), url(../fonts/Aller/Aller_Rg.ttf) format('truetype');
}
* {
  box-sizing: border-box;
  outline: none;
  font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  margin: 0;
  padding: 0;
  font-family: 'IBM Plex Sans', sans-serif;
}
img {
  width: 100%;
  height: 100%;
}
a {
  cursor: url(http://aradesign.in/content/common/gray-cursor.png) 15 13, default;
}
body {
  cursor: url(http://aradesign.in/content/common/white-cursor.png) 15 13, default;
  padding: 0;
  margin: 0;
  background: #000;
}
body .main-container {
  height: 100vh;
  position: relative;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 15px;
  z-index: 3;
}
@media only screen and (max-width: 768px) {
  body .main-container {
    padding: 0 25px;
  }
}
@media only screen and (max-width: 558px) {
  body .main-container {
    padding: 0 15px;
  }
}
.main-container {
  height: 100vh;
  position: relative;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 15px;
}
.main-container .row {
  width: 100%;
}
.main-container .row .container {
  width: 100%;
}
.main-container .row .container .grid {
  display: flex;
}
.main-container .row .container .grid .column.column-right {
  margin-left: auto;
}
.main-container .row .container .grid .column.column-right .menu img {
  width: 25px;
}
.main-container .super-container .row .container.project-details {
  padding: 0 15px;
}
.main-container .super-container .row .container.project-details .grid {
  height: 70vh;
  align-items: center;
}
@media only screen and (max-width: 768px) {
  .main-container .super-container .row .container.project-details .grid {
    display: block;
  }
}
.main-container .super-container .row .container.project-details .column-left {
  width: 65%;
}
@media only screen and (max-width: 768px) {
  .main-container .super-container .row .container.project-details .column-left {
    width: 100%;
  }
}
.main-container .super-container .row .container.project-details .column-left img {
  width: 100%;
}
.main-container .super-container .row .container.project-details .column-left .slick-dots {
  bottom: 0;
  margin-bottom: 10px;
  text-align: right;
}
.main-container .super-container .row .container.project-details .column-left .slick-dots li button {
  margin-right: 0px;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 16px solid #4d4d4d;
  transition: all 0.5s ease-in-out;
  border-radius: 2px;
  padding: 0;
}
.main-container .super-container .row .container.project-details .column-left .slick-dots li button:hover {
  border-bottom: 16px solid #fff;
}
.main-container .super-container .row .container.project-details .column-left .slick-dots li button::before {
  width: 0;
  color: transparent;
}
.main-container .super-container .row .container.project-details .column-left .slick-dots li.slick-active button {
  border-bottom: 16px solid #fff;
}
.main-container .super-container .row .container.project-details .column-right {
  width: 32%;
}
@media only screen and (max-width: 768px) {
  .main-container .super-container .row .container.project-details .column-right {
    width: 100%;
  }
}
.main-container .super-container .row .container.project-details .detail-content {
  margin-bottom: 25px;
  padding-left: 30px;
}
@media only screen and (max-width: 768px) {
  .main-container .super-container .row .container.project-details .detail-content {
    padding: 30px 0;
  }
}
.main-container .super-container .row .container.project-details .detail-content h2 {
  font-size: 32px;
  color: #fff;
  font-weight: 700;
  line-height: 38px;
  padding-bottom: 10px;
  margin: 0;
  text-align: right;
  font-family: 'Montserrat', sans-serif;
}
@media only screen and (max-width: 768px) {
  .main-container .super-container .row .container.project-details .detail-content h2 {
    text-align: left;
  }
}
.main-container .super-container .row .container.project-details .detail-content .tags {
  padding: 5px 0 15px;
}
.main-container .super-container .row .container.project-details .detail-content .tags span {
  font-size: 16px;
  display: block;
  color: #fff;
  font-weight: 400;
  line-height: 18px;
  margin: 10px 0;
  text-align: right;
  font-family: 'Montserrat', sans-serif;
}
@media only screen and (max-width: 768px) {
  .main-container .super-container .row .container.project-details .detail-content .tags span {
    text-align: left;
  }
}
.main-container .super-container .row .container.project-details .detail-content p {
  font-size: 16px;
  color: #fff;
  font-weight: 400;
  line-height: 23px;
  margin: 0;
  text-align: right;
  font-family: 'Montserrat', sans-serif;
}
@media only screen and (max-width: 768px) {
  .main-container .super-container .row .container.project-details .detail-content p {
    text-align: left;
  }
}
.main-container .super-container .slider-popup {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  background: rgba(0, 0, 0, 0.7);
  justify-content: center;
}
.main-container .super-container .slider-popup .close-menu {
  padding: 40px 0;
  position: absolute;
  top: 0;
  right: 0;
}
.main-container .super-container .slider-popup .close-menu .close {
  display: flex;
  padding: 25px;
}
.main-container .super-container .slider-popup .close-menu .close.cross {
  position: absolute;
  right: 40px;
  top: 40px;
  width: 75px;
  height: 75px;
  padding: 0;
}
.main-container .super-container .slider-popup .close-menu .close.cross:hover {
  opacity: 1;
}
.main-container .super-container .slider-popup .close-menu .close.cross:before,
.main-container .super-container .slider-popup .close-menu .close.cross:after {
  position: absolute;
  left: 28px;
  top: -13px;
  content: ' ';
  height: 35px;
  width: 2px;
  background-color: #fff;
  opacity: 1;
}
.main-container .super-container .slider-popup .close-menu .close.cross:before {
  transition: all 0.5s ease-in-out;
  transform: rotate(-90deg);
}
.main-container .super-container .slider-popup .close-menu .close.cross:after {
  transition: all 0.5s ease-in-out;
  transform: rotate(-180deg);
}
.main-container .super-container .slider-popup .close-menu .close.cross.active:before {
  transform: rotate(45deg);
}
.main-container .super-container .slider-popup .close-menu .close.cross.active:after {
  transform: rotate(-45deg);
}
.main-container .super-container .slider-popup .close-menu .close span {
  margin-left: auto;
  font-size: 21px;
  position: absolute;
  z-index: 2;
  background: #fff;
  font-size: 18px;
}
.main-container .super-container .slider-popup .slider-inner {
  width: 70%;
  margin: 0 auto;
}
