.header-section {
  padding: 25px 0px;
}
@media only screen and (max-width: 768px) {
  .header-section {
    padding: 20px 0px;
  }
}
@media only screen and (max-width: 558px) {
  .header-section {
    padding: 15px 0px;
  }
}
.header-section .grid {
  display: flex;
  align-items: center;
}
.header-section .grid .column.column-right {
  margin-left: auto;
}
.header-section .grid .column.column-right .menu img {
  width: 25px;
}
.screen-pattern {
  background: transparent;
  background-image: url("http://aradesign.in/content/common/bg-triangle.png");
  position: fixed;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  z-index: 2;
}
