}
@media only screen and (max-width: 768px) {
  .main-container .super-container .row .container.home-container .left-content .title-text .rw-sentence {
    display: block;
  }
}
@media only screen and (max-width: 558px) {
  .main-container .super-container .row .container.home-container .left-content .title-text .rw-sentence {
