.screen__video {
  overflow: hidden;
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  width: 100vw;
  height: 100vh;
}
.screen__video:before {
  content: '';
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
}
.screen__video video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  font-family: 'object-fit: cover;';
  position: absolute;
  z-index: 1;
  opacity: 1;
}
@keyframes move {
  0% {
    top: 0px;
  }
  20% {
    top: -32px;
  }
  40% {
    top: -64px;
  }
  60% {
    top: -96px;
  }
  80% {
    top: -128px;
  }
}
