.header-section .menu {
  transform: rotate(0deg);
  transition: all 0.5s ease-in-out;
}
.header-section .menu img {
  transform: rotate(0deg);
}
.header-section .menu img {
  transition: all 0.5s ease-in-out;
}
.header-section .menu.none {
  display: none;
}
.header-section .menu.active {
  transform: rotate(180deg);
}
.header-section .menu.active img {
  transform: rotate(180deg);
}
.menu-wrapper {
  position: fixed;
  max-width: 280px;
  width: 100%;
  background: rgba(0, 0, 0, 0.6);
  right: -420px;
  bottom: 0;
  top: 0;
  transition: all 0.5s ease-in-out;
  z-index: 2;
}
@media only screen and (max-width: 768px) {
  .menu-wrapper {
    max-width: 100%;
    right: -100%;
  }
}
.menu-wrapper.show {
  right: 0;
}
.menu-wrapper .close-menu {
  padding: 40px 0;
}
.menu-wrapper .close-menu .close {
  display: flex;
  padding: 25px;
  cursor: url(http://aradesign.in/content/common/red-cursor.png) 15 13, default;
}
.menu-wrapper .close-menu .close.cross {
  position: absolute;
  right: 40px;
  top: 40px;
  width: 75px;
  height: 75px;
  padding: 0;
}
@media only screen and (max-width: 768px) {
  .menu-wrapper .close-menu .close.cross {
    width: 30px;
    height: 30px;
  }
}
.menu-wrapper .close-menu .close.cross:hover {
  opacity: 1;
}
.menu-wrapper .close-menu .close.cross:before,
.menu-wrapper .close-menu .close.cross:after {
  position: absolute;
  left: 28px;
  top: -13px;
  content: ' ';
  height: 35px;
  width: 2px;
  background-color: #fff;
  opacity: 1;
}
.menu-wrapper .close-menu .close.cross:before {
  transition: all 0.5s ease-in-out;
  transform: rotate(-90deg);
}
.menu-wrapper .close-menu .close.cross:after {
  transition: all 0.5s ease-in-out;
  transform: rotate(-180deg);
}
.menu-wrapper .close-menu .close.cross.active:before {
  transform: rotate(45deg);
}
.menu-wrapper .close-menu .close.cross.active:after {
  transform: rotate(-45deg);
}
.menu-wrapper .close-menu .close span {
  margin-left: auto;
  font-size: 21px;
  position: absolute;
  z-index: 2;
  background: #fff;
  font-size: 18px;
  cursor: url(http://aradesign.in/content/common/red-cursor.png) 15 13, default;
}
.menu-wrapper .close p {
  font-size: 14px;
  color: #fff;
  line-height: 18px;
  font-family: 'Montserrat', sans-serif;
  margin-bottom: 20px;
}
.menu-wrapper .close.bottom {
  position: absolute;
  bottom: 0;
  left: 0px;
  margin-bottom: 25px;
  padding: 0 25px;
  width: 100%;
}
.menu-wrapper .close.bottom span {
  text-align: right;
  display: block;
  color: #fff;
  font-family: 'IBM Plex Sans', sans-serif;
}
.menu-wrapper .close.bottom span img {
  width: 25px;
}
.menu-wrapper .nav {
  padding: 30px;
  height: 85vh;
}
.menu-wrapper .nav .menu {
  display: flex;
  align-items: center;
  padding: 10px 0;
  -webkit-animation: drop 0.9s;
  animation: drop 0.9s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  opacity: 1;
}
.menu-wrapper .nav .menu:nth-child(2) {
  -webkit-animation-delay: 0.7s;
  animation-delay: 0.7s;
}
.menu-wrapper .nav .menu:nth-child(3) {
  -webkit-animation-delay: 0.5s;
  animation-delay: 0.5s;
}
.menu-wrapper .nav .menu:nth-child(4) {
  -webkit-animation-delay: 1s;
  animation-delay: 1s;
}
.menu-wrapper .nav .menu:first-child {
  margin-top: 0;
}
.menu-wrapper .nav .menu span.icon {
  height: 18px;
  margin-right: 15px;
  cursor: url(http://aradesign.in/content/common/red-cursor.png) 15 13, default;
}
.menu-wrapper .nav .menu span.icon img {
  width: 18px !important;
}
.menu-wrapper .nav .menu .name {
  font-size: 16px;
  color: #fff;
  font-family: 'IBM Plex Sans', sans-serif;
  cursor: url(http://aradesign.in/content/common/red-cursor.png) 15 13, default;
}
@-webkit-keyframes drop {
  from {
    top: 0px;
  }
  70% {
    top: 165px;
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  to {
    top: 150px;
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
}
@keyframes drop {
  from {
    top: 0px;
  }
  70% {
    top: 165px;
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  to {
    top: 150px;
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
}
